// src/pages/product/[id].js
import { useRouter } from "next/router";
import { useEffect, useState, useRef } from "react";
import Link from "next/link";

function getCart() {
  try { return JSON.parse(localStorage.getItem("livemart_cart") || "[]"); } catch (e) { return []; }
}
function saveCart(cart) { localStorage.setItem("livemart_cart", JSON.stringify(cart)); }

export default function ProductDetailPage() {
  const router = useRouter();
  const { id } = router.query;
  const [product, setProduct] = useState(null);
  const [qty, setQty] = useState(1);
  const prevQtyRef = useRef(1); // store last valid qty
  const [selectedSize, setSelectedSize] = useState(null);
  const [selectedSizeStock, setSelectedSizeStock] = useState(0);
  const [loading, setLoading] = useState(true);
  const [adding, setAdding] = useState(false);

  useEffect(() => {
    if (!id) return;
    let cancelled = false;
    async function load() {
      setLoading(true);
      try {
        const res = await fetch(`/api/products/${id}`);
        if (res.ok) {
          const p = await res.json();
          if (!cancelled) {
            setProduct(p);
            if (p.sizes && p.sizes.length) {
              setSelectedSize(p.sizes[0].label);
              setSelectedSizeStock(p.sizes[0].stock || 0);
              setQty(1);
              prevQtyRef.current = 1;
            }
          }
        } else {
          setProduct(null);
        }
      } catch (e) {
        console.error(e);
      } finally {
        if (!cancelled) setLoading(false);
      }
    }
    load();
    return () => { cancelled = true; };
  }, [id]);

  useEffect(() => {
    if (!product) return;
    const s = (product.sizes || []).find(x => x.label === selectedSize);
    const stock = s ? (s.stock || 0) : 0;
    setSelectedSizeStock(stock);
    // if the previously stored qty is greater than new stock, keep prev valid but do not auto-reduce
    if (qty > stock && stock > 0) {
      // keep qty but inform user next time they try to increase
      // do nothing here (we don't change qty silently)
    }
  }, [product, selectedSize]);

  // NEW: controlled qty input handler that warns user instead of clamping silently
  function handleQtyInputChange(e) {
    const raw = e.target.value;
    const n = Number(raw || 0);
    if (!Number.isFinite(n) || n <= 0) {
      // ignore invalid numbers; do not update prevQtyRef
      setQty(1);
      prevQtyRef.current = 1;
      return;
    }

    // if there's a size-based stock restriction
    if (selectedSize) {
      if (n > selectedSizeStock) {
        // show popup and DO NOT change qty — keep previous valid qty
        alert(`Maximum available units for size "${selectedSize}" is ${selectedSizeStock}.`);
        // restore previous qty value in the input display
        setQty(prevQtyRef.current);
        return;
      }
    } else {
      // top-level stock check if used
      // if product.stock is defined, enforce it
      if (typeof product?.stock === "number") {
        if (n > product.stock) {
          alert(`Maximum available units is ${product.stock}.`);
          setQty(prevQtyRef.current);
          return;
        }
      }
    }

    // valid value within stock — accept it and update prevQtyRef
    setQty(n);
    prevQtyRef.current = n;
  }

  async function handleAddToCart() {
    if (!product) { alert("Product not loaded"); return; }
    const desiredQty = Number(qty) || 1;
    setAdding(true);
    try {
      // fetch latest product to avoid stale data
      const res = await fetch(`/api/products/${product._id}`);
      if (!res.ok) { alert("Failed to verify stock. Try again."); setAdding(false); return; }
      const latest = await res.json();

      if (selectedSize) {
        const sizeObj = (latest.sizes || []).find(s => s.label === selectedSize);
        const available = sizeObj ? Number(sizeObj.stock || 0) : 0;
        if (available <= 0) { alert(`Selected size ${selectedSize} is out of stock.`); setAdding(false); return; }
        if (desiredQty > available) { alert(`Only ${available} left for size ${selectedSize}.`); setAdding(false); return; }
      } else {
        if (typeof latest.stock === "number") {
          const available = Number(latest.stock || 0);
          if (available <= 0) { alert("Product out of stock."); setAdding(false); return; }
          if (desiredQty > available) { alert(`Only ${available} left.`); setAdding(false); return; }
        }
      }

      const cart = getCart();
      const existing = cart.find(i => i.productId === product._id && i.size === selectedSize);
      if (existing) existing.qty = Math.min(existing.qty + desiredQty, 999);
      else cart.push({
        productId: product._id,
        name: product.name,
        price: product.price,
        size: selectedSize,
        qty: desiredQty,
        image: product.images && product.images[0]
      });
      saveCart(cart);
      router.push("/cart");
    } catch (err) {
      console.error(err);
      alert("Error adding to cart");
    } finally {
      setAdding(false);
    }
  }

  if (loading) return <p style={{ padding: 20 }}>Loading…</p>;
  if (!product) return <p style={{ padding: 20 }}>Product not found.</p>;

  return (
    <div style={{ padding: 20, fontFamily: "system-ui, sans-serif" }}>
      <header style={{ display: "flex", justifyContent: "space-between" }}>
        <div><Link href="/categories">Categories</Link> &nbsp;/&nbsp; <Link href="/products">Products</Link></div>
        <Link href="/cart">Cart</Link>
      </header>

      <main style={{ display: "flex", gap: 24, marginTop: 18 }}>
        <div style={{ width: 360 }}>
          <div style={{ background: "#fafafa", padding: 12, borderRadius: 8 }}>
            <img src={product.images && product.images[0] ? product.images[0] : "/images/placeholder.png"} alt={product.name} style={{ width: "100%", objectFit: "contain" }} />
          </div>
        </div>

        <div style={{ flex: 1 }}>
          <h1 style={{ marginTop: 0 }}>{product.name}</h1>
          <div style={{ color: "#444", marginBottom: 12 }}>{product.description}</div>
          <div style={{ fontSize: 20, fontWeight: 700 }}>₹{product.price}</div>

          {product.sizes && product.sizes.length > 0 && (
            <div style={{ marginTop: 12 }}>
              <div style={{ marginBottom: 6 }}>Size</div>
              <select value={selectedSize || ""} onChange={e => { setSelectedSize(e.target.value); setQty(1); prevQtyRef.current = 1; }} style={{ padding: 8, borderRadius: 6 }}>
                {product.sizes.map(s => <option key={s.label} value={s.label}>{s.label} — stock: {s.stock}</option>)}
              </select>
              <div style={{ fontSize: 13, color: "#666", marginTop: 6 }}>{selectedSize ? `Available: ${selectedSizeStock}` : null}</div>
            </div>
          )}

          <div style={{ marginTop: 12, display: "flex", gap: 8, alignItems: "center" }}>
            <input
              type="number"
              min="1"
              value={qty}
              onChange={handleQtyInputChange}
              style={{ width: 80, padding: 8, borderRadius: 6 }}
            />
            <button onClick={handleAddToCart} disabled={adding} style={{ padding: "10px 14px", borderRadius: 8, background: "#0070f3", color: "#fff", border: "none", cursor: "pointer" }}>
              {adding ? "Adding…" : "Add to cart"}
            </button>
          </div>
        </div>
      </main>
    </div>
  );
}
